  function countdown() {
    let timer
    const nowTime = Date.now()
    const end = endTime && new Date(endTime.replace(/-/g, '/')).getTime()
    if (endTime && nowTime < end) {
      const oneHour = 60 * 60
      const oneDay = 24 * oneHour
      const s = (end - nowTime) / 1000
      const days = Math.floor(s / oneDay)
      const hours = Math.floor(s % oneDay / oneHour)
      const minutes = Math.floor(s % oneDay % oneHour / 60)
      const seconds = Math.floor(s % oneDay % oneHour % 60)
      timer = setTimeout(() => {
        setActivityDate(() => ({
          days: days.toString(),
          hours: hours > 9 ? hours : '0' + hours,
          minutes: minutes > 9 ? minutes : '0' + minutes,
          seconds: seconds > 9 ? seconds : '0' + seconds
        }))
        countdown()
        clearTimeout(timer)
      }, 1000)
    } else {
      setActivityDate({})
    }
    return () => clearTimeout(timer)
  }

