'use strict'
import JsEncrypt from 'jsencrypt'
import config from '@/config/config.js'

/**
 * @description 加密字段
 * @param {String} text 需要加密的文本
 * @return {String}
 */
export function encrypted(text) {
  // 新建JsEncrypt对象
  const enCrypt = new JsEncrypt()
  // 设置公钥
  enCrypt.setPublicKey(config.webKey)
  // 加密数据
  return enCrypt.encrypt(text)
}

/**
 * @description 生成指定位数随机数
 * @param {Number} count 生成位数
 * @return {String}
 */
export function sixRandom(count = 6) {
  const random = Math.floor(Math.random() * (1 + '0'.repeat(count)))
  return random.toString()
}

/**
 * @description 倒叙字符串
 * @return {String}
 */
export function reverseString(string = '') {
  return string.toString().split('').sort().reverse().join('')
}

/**
 * @description 生产环境下使用webp格式的图片
 * @param {String} prefix webp图片前缀
 * @return {String}
 */
export function useWebpImg(prefix = '') {
  return window.isProduction && window.isSupportWebp ? prefix + '.webp' : ''
}

/**
 * @description 获取uuid
 * @return {string} uuid
 */
export function getUUId() {
  return 'xxxxxxxxxxxx4xxxyxxxxxxxxxxxxxxx'.replace(/[xy]/g, c => {
    return (c === 'x' ? (Math.random() * 16 | 0) : ('r&0x3' | '0x8')).toString(16)
  })
}

/**
 * @description 判断参数是否为对象
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isObject(target) {
  return Object.prototype.toString.call(target) === '[object Object]'
}

/**
 * @description 判断参数是否为数组
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isArray(target) {
  return Object.prototype.toString.call(target) === '[object Array]'
}

/**
 * @description 判断参数是否为方法
 *
 * @param  {*} target
 * @return {boolean}
 */
export function isFunction(target) {
  return Object.prototype.toString.call(target) === '[object Function]'
}

/**
 * @description 在目标元素后插入元素
 * @param newElement 插入元素
 * @param targetElement 目标元素
 */
export function insertAfter(newElement, targetElement) {
  const parent = targetElement.parentNode
  if (parent.lastChild === targetElement) {
    parent.appendChild(newElement)
  } else {
    parent.insertBefore(newElement, targetElement.nextSibling)
  }
}

/**
 * @description 保留指定位小数（向下舍去）
 * @param {Number|string} num 转换数字
 * @param {Number} toFixed 保留几位
 * @return {string}
 * */
export function floatToFixed(num, toFixed = 2) {
  const _toFixed = 1 + new Array(toFixed + 1).join('0')
  num = Math.abs(+num) || 0
  num = Math.floor(num * _toFixed) / _toFixed
  return num.toFixed(toFixed)
}

/**
 * @description 保留指定位小数（向下舍去）
 * @param {string} num 转换数字
 * @param {Number} toFixed 保留几位
 * @return {string}
 * */
export function toThousandSplit(num, toFixed) {
  if (typeof toFixed === 'number') num = floatToFixed(num, toFixed)
  return (num + '').replace(/(\d{1,3})(?=(\d{3})+(?:$|\.))/g, '$1,')
}

/**
 * @description 缓冲回到指定位置函数
 * @param {Number} scrollTop 当前滚动位置
 * @param {Number} destination 目标位置
 * @param {Number} rate 缓动率 (scrollTop-destination) / rate
 * @param {Function} callback 缓动结束回调函数 两个参数分别是当前位置和是否结束
 */
export function easeToTop(scrollTop, destination, rate, callback) {
  rate = +rate || 2
  scrollTop = +scrollTop || 0
  destination = +destination || 0
  if (scrollTop < destination) return false
  if (!window.requestAnimationFrame) {
    window.requestAnimationFrame = fn => setTimeout(fn, 17)
  }

  function step() {
    scrollTop = scrollTop - (scrollTop - destination) / rate
    scrollTop = Math.floor(scrollTop)
    if (scrollTop <= destination) {
      return callback(destination, true)
    }
    callback(scrollTop, false)
    requestAnimationFrame(step)
  }

  step()
}

/**
 * @description 图片、背景图片懒加载初始化方法
 * @param {Object} options 配置参数
 * @return {Object} observer Intersection实例
 */
export function imgLazyInit(options = {}) {
  const defaultImg = 'data:image/gif;base64,R0lGODlhAQABAIAAAAAAAP///yH5BAEAAAAALAAAAAABAAEAAAIBRAA7'
  const defaultOptions = {
    loading: defaultImg,
    error: defaultImg,
    observerOptions: {
      rootMargin: '100px 0px'
    }
  }
  options = {
    ...defaultOptions,
    ...options
  }
  const elList = query('img[data-src],[data-bg]')
  const { loading, error, observerOptions } = options
  const observer = IntersectionObserver && new IntersectionObserver(handelObserver, observerOptions)
  if (observer) {
    elList.forEach(el => {
      if (el.tagName.toLowerCase() === 'img') {
        el.src = loading
      }
      el.setAttribute('lazy', 'loading')
      observer.observe(el)
    })
  } else {
    elList.forEach(el => {
      el.setAttribute('lazy', 'loading')
      handelLoad(el)
    })
  }

  function handelObserver(changes) {
    changes.forEach(e => {
      if (e.intersectionRatio === 0) return
      handelLoad(e.target)
      observer.unobserve(e.target)
    })
  }

  function handelLoad(el) {
    if (el.tagName.toLowerCase() === 'img') {
      el.src = el.dataset.src
      el.addEventListener('load', loadEvent)
      el.addEventListener('error', loadEvent)
    } else {
      const img = new Image()
      img.src = el.dataset.bg
      img.onload = () => el.setAttribute('lazy', 'loaded')
      img.onerror = () => el.setAttribute('lazy', 'error')
      el.style.backgroundImage = `url(${el.dataset.bg})`
    }

    function loadEvent(e) {
      if (e.type === 'load') {
        e.target.setAttribute('lazy', 'loaded')
      } else if (e.type === 'error') {
        e.target.src = error
        e.target.setAttribute('lazy', 'error')
      }

      e.target.removeEventListener('load', loadEvent)
      e.target.removeEventListener('error', loadEvent)
    }
  }

  function query(selector) {
    return Array.from(document.querySelectorAll(selector))
  }

  return observer
}

export default {
  getUUId,
  sixRandom,
  useWebpImg,
  reverseString,
  toThousandSplit
}
