import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { CSSTransition } from 'react-transition-group'
import classNames from 'classnames/bind'
import { toggleSideBar } from '@/store/actions/common.js'

import scss from './sidebar.module.scss'

const cx = classNames.bind(scss)

function mapStateToProps(state) {
  return { showSideBar: state.showSideBar }
}

function SideBar(props) {
  const { showSideBar, dispatch } = props
  const className = cx('side-bar', {
    open: showSideBar
  })
  document.body.style.overflow = showSideBar ? 'hidden' : null

  return (
    <div className={className}>
      <div className={scss['side-bar__inner']}>
        <div className={scss['side-bar__scroll']}>
          <div className={scss['side-bar__content']}>
            <button onClick={() => dispatch(toggleSideBar())}>关闭弹窗</button>
            <span>×</span>
          </div>
        </div>
      </div>
      <CSSTransition in={showSideBar} unmountOnExit timeout={300} classNames="fade">
        <div onClick={() => dispatch(toggleSideBar())} className={scss['side-bar__mask']}/>
      </CSSTransition>
    </div>
  )
}

SideBar.propTypes = {
  dispatch: PropTypes.func.isRequired,
  showSideBar: PropTypes.bool.isRequired
}

export default connect(mapStateToProps)(SideBar)
