import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import api from '@/api/api'
import { useWebpImg } from '@/utils/util'
import Banner from '@/components/banner/Banner'
import DewFigure from '@/views/home/dew_figure/DewFigure'
import Baccarat from '@/views/home/baccarat/Baccarat'
import Promo from '@/views/home/promo/Promo'
import { dialog } from '@/components/dialog/Dialog'
import scss from './home.module.scss'

const cx = classNames.bind(scss)

class Home extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      onlineNum: null
    }
  }

  openDialog() {
    dialog('测试弹窗')
  }

  openTwoDialog() {
    dialog({
      icon: 'success',
      title: '测试同时打开多个弹窗',
      text: '每个按钮都可以设置相应的回调',
      buttons: [{
        type: 'solid',
        text: '打开第二个提示弹窗',
        callback() {
          dialog('第二个弹窗')
          return false
        }
      }, {
        type: 'hollow',
        text: '关闭弹窗'
      }]
    })
  }

  render() {
    return (
      <main className={cx('home')}>
        <div className={cx('home__banner')}>
          <Banner field={'home'}/>
        </div>
        <div className={cx('home__games', 'clearfix')}>
          <div className={cx('games__item', 'default-bg-small', 'fl')}>
            <img className={cx('games__img')} src={require('./img/agqj.jpg' + useWebpImg())} alt="AG旗舰"/>
            <div className={cx('games__info')}>
              <p className={cx('games__title')}>
                AG旗舰
                <img className={cx('games__icon-exclusive')} src={require('./img/icon_exclusive.png')} alt=""/>
              </p>
              <p className={cx('games__subtitle')}>单注最高200万 豪客首选</p>
              <p className={cx('games__subtitle')}>多台同时下注 盈利翻倍</p>
              <p className={cx('games__online')}>
                <i className={cx('games__icon-online')}/>
                <span className={cx('games__online-num')}>{this.state.onlineNum || '--'}人</span>
              </p>
              <button onClick={this.openDialog} className={cx('games__button', 'home__button')}>
                免费试玩
              </button>
            </div>
            <div className={cx('games__mask')}/>
          </div>
          <div className={cx('games__item', 'default-bg-small', 'fr')}>
            <img className={cx('games__img')} src={require('./img/agin.jpg' + useWebpImg())} alt="AG旗舰"/>
            <div className={cx('games__info')}>
              <p className={cx('games__title')}>AG国际厅</p>
              <p className={cx('games__subtitle')}>美女主播互动综合游戏区</p>
              <p className={cx('games__subtitle')}>2万在线玩家同场竞技</p>
              <button onClick={this.openTwoDialog} className={cx('games__button', 'home__button')}>
                免费试玩
              </button>
            </div>
            <div className={cx('games__mask')}/>
          </div>
        </div>
        <div className={cx('home__dew')}>
          <p className={cx('home__module-title')}>百家乐好路推荐</p>
          <DewFigure/>
        </div>
        <div className={cx('home__baccarat')}>
          <p className={cx('home__module-title', 'no-margin')}>百家乐专家</p>
          <p className={cx('home__module-subtitle')}>注：仅展示凯发专属-旗舰厅数据，每小时更新一次。</p>
          <Baccarat/>
        </div>
        <div className={cx('home__promo')}>
          <Promo/>
        </div>
        <div className={cx('home__vip')}>
          <p className={cx('home__module-title')}>顶级待遇</p>
          <div className={cx('clearfix')}>
            <div className={cx('vip__item', 'default-bg-small', 'fl')}>
              <div className={cx('vip__info')}>
                <p className={cx('vip__title')}>全民俱乐部</p>
                <p className={cx('vip__subtitle')}>月月分红 终身享有</p>
                <button className={cx('vip__button', 'home__button')}>
                  立即加入
                </button>
              </div>
              <div className={cx('vip__mask')}/>
              <img className={cx('vip__img')} data-src={require('@/views/home/img/club.jpg' + useWebpImg())} alt=""/>
            </div>
            <div className={cx('vip__item', 'default-bg-small', 'fr')}>
              <div className={cx('vip__info')}>
                <p className={cx('vip__title')}>VIP专区</p>
                <p className={cx('vip__subtitle')}>绿色通道 贵族体验</p>
                <button className={cx('vip__button', 'home__button')}>
                  立即加入
                </button>
              </div>
              <div className={cx('vip__mask')}/>
              <img className={cx('vip__img')} data-src={require('@/views/home/img/vip.png' + useWebpImg())} alt=""/>
            </div>
          </div>
        </div>
        <div className={cx('home__group')} data-bg={require('@/views/home/img/group_bg.jpg' + useWebpImg())}>
          <div className={cx('group__inner')}>
            <p className={cx('home__module-title')}>凯发集团旗下</p>
            <div className={cx('group__icons')}>
              <span className={cx('group__icon', 'video-game')}/>
              <span className={cx('group__icon', 'e-sports')}/>
              <span className={cx('group__icon', 'chess')}/>
              <a className={cx('group__icon', 'vina')} href="javascript:void (0);"/>
              <a className={cx('group__icon', 'media')} href="javascript:void (0);"/>
              <a className={cx('group__icon', 'fund')} href="javascript:void (0);"/>
            </div>
          </div>
        </div>
      </main>
    )
  }

  componentDidMount() {
    const params = { _isNeedLoading: false }
    api.getOnlineNum(params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        this.setState({
          onlineNum: body
        })
      }
    })
  }
}

Home.propTypes = {
  history: PropTypes.object.isRequired
}

export default Home
