import React, { useEffect, useMemo, useRef, useState } from 'react'
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { isFunction, getUUId } from '@/utils/util.js'
import scss from './dialog.module.scss'

const cx = classNames.bind(scss)

function Dialog(props) {
  const { visible, params = {}, instanceId } = props
  const dialog = useRef(null)
  const dialogBox = useRef(null)
  const [enter, setEnter] = useState(false)
  const [exitDone, setExitDone] = useState(!visible)
  const [internalVisible, setInternalVisible] = useState(visible)
  const icons = {
    success: require('@/assets/img/common/icon_success50x50.png')
  }
  const { icon, title, text, buttons, callback, clickCloseCallback } = params
  const buttonsElement = useMemo(getButtonsEl, [buttons])
  const dialogClassName = cx('dialog', {
    'fade-enter': visible || internalVisible,
    'fade-exit': !internalVisible
  }, [
    enter ? 'fade-enter-active' : 'fade-exit-active'
  ])
  const dialogBoxClassName = cx('dialog__box', {
    'dialog__box--tips': instanceId,
    'alert-enter': visible || internalVisible,
    'alert-exit': !internalVisible
  }, [
    enter ? 'alert-enter-active' : 'alert-exit-active'
  ])
  const style = !visible && exitDone ? { display: 'none' } : {}

  useEffect(() => {
    const dialogs = document.getElementsByClassName(cx('dialog'))
    if (dialogs.length > 1) {
      dialog.current.style.backgroundColor = 'transparent'
    }
  }, [])

  useEffect(() => setInternalVisible(visible), [visible])

  useEffect(toggleDialog, [internalVisible, instanceId])

  function getButtonsEl() {
    if (buttons && buttons.length) {
      return buttons.map((item, index) => (
        <button onClick={() => handelClick(item.callback)} key={index.toString()}
          className={cx('dialog__button', `dialog__button--${item.type}`)}>
          {item.text}
        </button>
      ))
    } else {
      return (
        <button className={cx('dialog__button', 'dialog__button--solid')}
          onClick={() => handelClick(callback)}>
          确定
        </button>
      )
    }
  }

  function handelClick(callback) {
    if (isFunction(callback)) {
      if (callback() !== false) instanceId && setInternalVisible(false)
    } else {
      setInternalVisible(false)
    }
  }

  function handelClickClose() {
    handelClick(clickCloseCallback)
  }

  function removeClassName(el, className, status) {
    let removeClass
    if (status) {
      removeClass = cx(`${className}-enter`, `${className}-enter-active`)
    } else {
      removeClass = cx(`${className}-exit`, `${className}-exit-active`)
    }
    el.className = el.className.replace(removeClass, '')
  }

  function toggleDialog() {
    const _setTimeOut = requestAnimationFrame || setTimeout
    const _clearTimeout = cancelAnimationFrame || clearTimeout

    const timer = _setTimeOut(() => {
      setEnter(internalVisible)
      setTimeout(handel, 300)
    })

    function handel() {
      removeClassName(dialog.current, 'fade', internalVisible)
      removeClassName(dialogBox.current, 'alert', internalVisible)
      setExitDone(!internalVisible)
      if (!internalVisible && instanceId) {
        const container = document.getElementById(instanceId)
        ReactDOM.unmountComponentAtNode(container)
        container.parentNode.removeChild(container)
      }
    }

    return () => _clearTimeout(timer)
  }

  return (
    <div ref={dialog} style={style} className={dialogClassName}>
      <div ref={dialogBox} className={dialogBoxClassName}>
        <div className={cx('dialog__header')}>
          <span>{title}</span>
          <button onClick={handelClickClose} className={cx('dialog__close')}>×</button>
        </div>
        {
          props.children ||
          <div className={cx('dialog__content')}>
            {
              icons[icon] &&
              <div className={cx('dialog__icon-wrapper')}>
                <img className={cx('dialog__icon')} src={icons[icon]} alt=""/>
              </div>
            }
            <p className={cx('dialog__text')}>{text}</p>
            <div className={cx('dialog__button-wrapper')}>
              {buttonsElement}
            </div>
          </div>
        }
      </div>
    </div>
  )
}

Dialog.propTypes = {
  hide: PropTypes.func, // 销毁实例方法
  visible: PropTypes.bool.isRequired, // 控制弹窗生成或销毁 （关闭或显示）
  instanceId: PropTypes.string, // 函数调用生成的弹窗实例id
  children: PropTypes.object, // 子元素
  params: PropTypes.shape({
    icon: PropTypes.string,
    title: PropTypes.string,
    text: PropTypes.string,
    buttons: PropTypes.array,
    callback: PropTypes.func,
    clickCloseCallback: PropTypes.func
  })
}

function _init(instanceId, params) {
  let container
  const props = {
    params,
    instanceId,
    visible: true
  }
  container = document.getElementById(instanceId)
  if (!container) {
    container = document.createElement('div')
    document.body.appendChild(container)
  }
  container.setAttribute('id', instanceId)
  ReactDOM.render(<Dialog {...props}/>, container)
}

function _hide(instanceId, params) {
  const props = {
    params,
    instanceId,
    visible: false
  }
  const container = document.getElementById(instanceId)
  if (!container) return console.error(new Error('not found container'))
  ReactDOM.render(<Dialog {...props}/>, container)
}

/**
 * {{buttons: [{callback(): void, text: string, type: string}, {text: string, type: string}], icon: string, title: string}}
 * */
/**
 * @description js方法初始化话对话框组件
 * @param {Object} params
 *
 * params:{
 *   icon: {String} 需要显示的图标，默认值 null
 *   title：{String} 弹窗的标题，默认值 提示
 *   text：{String} 需要显示的文本 默认值 ''
 *   buttons：[ {Array} 需要渲染的多个按钮 默认值 [] （最多渲染2个按钮，此值若为空，默认渲染一个确定按钮）
 *     {
 *       text: {String} 按钮文字
 *       type：{String} 按钮类型 solid | hollow
 *       callback：{Function} 按钮点击事件 返回 false 可阻止弹窗关闭
 *     }
 *   ]
 *   callback：{Function} 默认渲染的确定按钮点击事件 返回 false 可阻止弹窗关闭
 *   clickCloseCallback: {Function} 关闭按钮点击事件 返回 false 可阻止弹窗关闭
 * }
 *
 * @param instanceId {string} 弹窗ID
 * @return {function} hide 关闭弹窗方法（用于业务自主控制弹窗关闭）
 * */
export function dialog(params = {}, instanceId) {
  const defaultParams = {
    icon: null,
    title: '提示',
    text: '',
    buttons: []
  }
  if (typeof params !== 'object') {
    params = { text: params }
  }
  if (typeof instanceId !== 'string') {
    instanceId = `_dialog_${getUUId()}`
  }
  params = {
    ...defaultParams,
    ...params
  }
  _init(instanceId, params)
  return _hide.bind(null, instanceId, params)
}

export default Dialog
