import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import { Router } from 'react-router'
import 'normalize.css/normalize.css'
import 'swiper/css/swiper.css'
import '@/assets/scss/base.scss'
import '@/assets/scss/common.scss'
import Routes, { history } from '../router/index.js'
import SideBar from '@/components/side_bar/SideBar.js'
import LoginDialog from '@/components/login_dialog/loginDialog.js'
import { getGameStatus } from '@/store/actions/common.js'

function mapStateToProps(state) {
  return {
    showLoginDialog: state.showLoginDialog
  }
}

class App extends React.Component {
  componentDidMount() {
    const params = { _isNeedLoading: false }
    this.props.dispatch(getGameStatus(params))
    const style = document.getElementById('loadingStyle')
    const div = document.getElementById('loadingEl')
    div.style.opacity = '0'
    div.addEventListener('transitionend', () => {
      style && style.parentNode.removeChild(style)
      div && div.parentNode.removeChild(div)
    })
  }

  render() {
    const { showLoginDialog } = this.props
    return (
      <Router history={history}>
        <Routes/>
        <SideBar/>
        {showLoginDialog && <LoginDialog/>}
      </Router>
    )
  }
}

App.propTypes = {
  dispatch: PropTypes.func.isRequired,
  showLoginDialog: PropTypes.bool.isRequired
}

export default connect(mapStateToProps)(App)
