'use strict'
import axios from 'axios'

const wmsServer = window.OMConfig.wmsServer
const wmsEnvPath = window.OMConfig.wmsEnvPath
const wmsCdnPath = window.OMConfig.wmsCdnPath

// https://fm.libo166.com/cdn/A02F/_wms/js/_l/electronicgames/games.js
// https://fm.libo166.com/cdn/A02F/_wms/static/_l/_data/_banner/banner.txt
// https://fm.libo166.com/cdn/A02F/_wms/img/_l/_banner/banner-index_new.jpg_.webp
// 获取wms静态文件
export function getWmsStatic(url, param) {
  url = `${wmsServer + wmsCdnPath}/_wms/static/${wmsEnvPath}/_data` + url
  return axios.get(url, param)
}

export default {
  getWmsStatic
}
