import React, { useRef, useEffect, useState } from 'react'
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import { insertAfter } from '@/utils/util.js'
import scss from './loading.module.scss'

const cx = classNames.bind(scss)

function Loading(props) {
  const { visible } = props
  const loading = useRef(null)
  const [enter, setEnter] = useState(false)
  const style = visible ? { display: null } : null
  const className = cx('loading', [visible ? 'fade-enter' : 'fade-exit', enter ? 'fade-enter-active' : 'fade-exit-active'])

  useEffect(() => {
    setEnter(visible)
    const current = loading.current
    const timer = setTimeout(handel, 300)

    function handel() {
      let removeClass
      let className = current.className
      if (visible) {
        removeClass = cx('fade-enter', 'fade-enter-active')
      } else {
        removeClass = cx('fade-exit', 'fade-exit-active')
        current.style.display = 'none'
      }
      className = className.replace(removeClass, '')
      current.className = className
    }

    return () => clearTimeout(timer)
  }, [visible])

  return (
    <div ref={loading} style={style} className={className}>
      <div className={cx('loading__logo')}/>
    </div>
  )
}

Loading.propTypes = {
  visible: PropTypes.bool.isRequired
}

function open(flag) {
  this.flag = flag
  let container = document.getElementById('_loading')
  if (!container) {
    container = document.createElement('div')
    container.setAttribute('id', '_loading')
    insertAfter(container, document.getElementById('root'))
  }
  ReactDOM.render(<Loading visible={true}/>, container)
}

function hide(flag) {
  const container = document.getElementById('_loading')
  if (!container || this.flag !== flag) return
  ReactDOM.render(<Loading visible={false}/>, container)
}

export function loading(flag) {
  loading.open(flag)
}

loading.open = open.bind(loading)
loading.hide = hide.bind(loading)

export default Loading
