import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'

import api from '@/api/api.js'
import { encrypted } from '@/utils/util.js'
import scss from './codeButton.module.scss'

const cx = classNames.bind(scss)

function areEqual(prevProps, nextProps) {
  return prevProps.enable === nextProps.enable
}

function CodeButton(props) {
  const [duration, setDuration] = useState(0)
  const [sending, setSending] = useState(false)
  const [isFirstSend, setIsFirstSend] = useState(true)
  const { enable, params, className, sendCodeBefore, sendCallBack } = props

  useEffect(countdown, [duration])

  function sendCode() {
    const _params = {
      ...params,
      mobileNo: encrypted(params.mobileNo)
    }
    if (sending || !sendCodeBefore()) return
    api.sendCode(_params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        setSending(true)
        setDuration(60)
        sendCallBack(body.messageId)
      } else {
        sendCallBack(null)
      }
      setIsFirstSend(false)
    }).catch(() => {
      sendCallBack(null)
    })
  }

  function countdown() {
    let timer = null
    if (duration <= 0) {
      setDuration(0)
      setSending(false)
    } else {
      timer = setTimeout(() => setDuration(duration - 1), 1000)
    }

    return () => clearTimeout(timer)
  }

  function getButtonText() {
    if (sending) {
      return `重新发送(<span class="monospaced-font">${duration}</span>)`
    } else if (!isFirstSend) {
      return '重新发送'
    } else {
      return '发送验证码'
    }
  }

  return (
    <button onClick={sendCode}
      dangerouslySetInnerHTML={{ __html: getButtonText() }}
      className={cx('code-button', { disabled: !enable || sending }, className)}>
    </button>
  )
}

CodeButton.defaultProps = {
  enable: true
}

CodeButton.propTypes = {
  className: PropTypes.string,
  enable: PropTypes.bool.isRequired,
  params: PropTypes.shape({
    use: PropTypes.number.isRequired,
    mobileNo: PropTypes.oneOfType([
      PropTypes.string.isRequired,
      PropTypes.number.isRequired
    ])
  }),
  sendCallBack: PropTypes.func.isRequired,
  sendCodeBefore: PropTypes.func.isRequired
}

export default React.memo(CodeButton, areEqual)
