import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import classNames from 'classnames/bind'
import api from '@/api/api.js'
import { floatToFixed } from '@/utils/util.js'
import { setSignOut } from '@/store/actions/sign.js'
import { toggleSideBar, toggleLoginDialog } from '@/store/actions/common.js'
import { dialog } from '@/components/dialog/Dialog.js'
import scss from './header.module.scss'

const cx = classNames.bind(scss)

function mapStateToProps(state) {
  return {
    showSideBar: state.showSideBar,
    customer: state.customer,
    loginStatus: (state => !!state.customer.loginName)(state),
    balance: (state => +state.balances.withdrawBal)(state)
  }
}

function LoginBefore(props) {
  const { dispatch } = props
  return (
    <div className={cx('header__login-before')}>
      <span>还不是会员？</span>
      <a className={cx('header__try-link')} href="javascript:void (0);">免费试玩</a>
      <div className={cx('header__sign')}>
        <a onClick={() => dispatch(toggleLoginDialog(true))} className={cx('header__sign-link')}
          href="javascript:void (0);">登录</a>
        /
        <a className={cx('header__sign-link')} href="javascript:void (0);">注册</a>
      </div>
    </div>
  )
}

function LoginAfter(props) {
  const { customer, balance, dispatch, history } = props

  function getLogout() {
    history.push('/')
    api.logout().then(({ head, body }) => {
      if (head.errCode === '0000') {
        dispatch(setSignOut(body.info))
      }
    })
  }

  function signOut() {
    dialog({
      title: '提示',
      text: '尊敬的贵宾，您确定要退出当前账号吗？',
      buttons: [{
        type: 'hollow',
        text: '确定退出',
        callback: getLogout
      }, {
        type: 'solid',
        text: '再玩一会'
      }]
    })
  }

  return (
    <div className={cx('header__login-after')}>
      <button onClick={() => history.push('/deposit')} className={cx('header__button')}>
        <i className={cx('header__button-icon', 'header__button-icon--deposit')}/>
        <span>存款</span>
      </button>
      <button onClick={() => history.push('/withdraw')} className={cx('header__button')}>
        <i className={cx('header__button-icon', 'header__button-icon--withdraw')}/>
        <span>取款</span>
      </button>
      <div className={cx('header__user')}>
        <div className={cx('header__user-info')}>
          <p>{customer.loginName}</p>
          <p className={cx('header__user-balance')}>
            ￥<span>{floatToFixed(balance)}</span>
          </p>
        </div>
        <i className={cx('header__user-avatar')}/>
        <i className={cx('header__icon-menu')}/>
        <div className={cx('header__user-menu')}>
          <a className={cx('header__menu-item')} href='javascript:void (0);'>
            <span>用户中心</span>
            <i className={cx('header__menu-icon', 'header__menu-icon--arrow')}/>
          </a>
          <a className={cx('header__menu-item')} href='javascript:void (0);'>
            <span>洗码</span>
            <i className={cx('header__menu-icon', 'header__menu-icon--arrow')}/>
          </a>
          <a onClick={signOut} className={cx('header__menu-item')} href='javascript:void (0);'>
            <span>退出</span>
            <i className={cx('header__menu-icon', 'header__menu-icon--logout')}/>
          </a>
        </div>
      </div>
    </div>
  )
}

function Header(props) {
  const { history, dispatch, showSideBar, loginStatus, customer, balance } = props
  const { location: { pathname } } = history
  const homeNav = [
    {
      name: '首页',
      path: '/'
    }, {
      name: '真人娱乐',
      path: '/games'
    }, {
      name: '电子游艺',
      path: '/promotions'
    }, {
      name: '体育竞技',
      path: '/home'
    }, {
      name: '彩票投注',
      path: '/home'
    }, {
      name: '真人棋牌',
      path: '/home'
    }, {
      name: '全民俱乐部',
      path: '/home'
    }, {
      name: '凯发风采',
      path: '/home'
    }, {
      name: '最新优惠',
      path: '/home'
    }
  ]
  const userCenterNav = [
    {
      name: '会员首页',
      path: '/'
    }, {
      name: '资金管理',
      path: '/'
    }, {
      name: '账号设置',
      path: '/'
    }, {
      name: '资金明细',
      path: '/'
    }, {
      name: '超级合伙人',
      path: '/'
    }
  ]
  const navList = pathname === '/myAccount' ? userCenterNav : homeNav
  const partners = [
    {
      name: '菲律宾国家队'
    }, {
      name: '马尔代夫国家队'
    }, {
      name: '德甲沙尔克'
    }, {
      name: '英冠卡迪夫城'
    }, {
      name: '意甲卡利亚里'
    }
  ]
  const className = cx('header', {
    'open-side-bar': showSideBar
  })

  return (
    <header className={className}>
      <div className={cx('header__inner', 'clearfix')}>
        <div className={cx('header__left')}>
          <button onClick={() => dispatch(toggleSideBar())} className={cx('header__side-bar-switch')}>
            <i className={cx('header__icon-side-bar')}/>
            <span>导航</span>
          </button>
          <div className={cx('header__partners')}>
            {partners.map((item, index) => {
              return (
                <a className={cx('header__partner-item', 'item-' + index)} key={index}>
                  <i className={cx('header__partner-logo', 'logo-' + index)}/>
                  <span className={cx('header__partner-title￿')}>{item.name}</span>
                </a>
              )
            })}
            <span className={cx('header__partner-title￿')}>官方合作伙伴</span>
          </div>
        </div>
        <a onClick={() => history.push('/')} className={cx('header__logo')} href="javascript:void (0);"/>
        <div className={cx('header__right')}>
          <div className={cx('header__message')}>
            <i className={cx('header__icon-message')}/>
            <div className={cx('header__message-box')}>
              <h4 className={cx('header__message-header')}>消息</h4>
              <div className={cx('header__message-content')}>
                <div className={cx('header__no-message')}>
                  <i className={cx('header__icon-no-message')}/>
                  <h6>暂时没有新的消息</h6>
                </div>
              </div>
            </div>
          </div>
          {
            loginStatus
              ? <LoginAfter balance={balance} customer={customer} dispatch={dispatch} history={history}/>
              : <LoginBefore dispatch={dispatch} history={history}/>
          }
        </div>
      </div>
      <ul className={cx('nav')}>
        {navList.map((item, index) => {
          return (
            <li onClick={() => history.push(item.path)} className={cx('nav__item')} key={index}>
              <a className={cx('nav__link', { active: pathname === item.path })}>
                {item.name}
              </a>
            </li>
          )
        })}
      </ul>
    </header>
  )
}

Header.propTypes = {
  dispatch: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  showSideBar: PropTypes.bool.isRequired,
  loginStatus: PropTypes.bool.isRequired,
  customer: PropTypes.object.isRequired,
  balance: PropTypes.number.isRequired
}
LoginAfter.propTypes = {
  dispatch: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired,
  customer: PropTypes.object.isRequired,
  balance: PropTypes.number.isRequired
}
LoginBefore.propTypes = {
  dispatch: PropTypes.func.isRequired,
  history: PropTypes.object.isRequired
}

export default connect(mapStateToProps)(Header)
