import React from 'react'
import loadable from '@loadable/component'
import { Switch, Route } from 'react-router'
import { createBrowserHistory } from 'history'

import home from '@/views/home/home.js'
import Header from '@/components/header/Header.js'
import Footer from '@/components/footer/Footer'
import PageContainer from '@/components/page_container/PageContainer.js'

const Error404 = loadable(() => import('@/views/common/Error404.js'))
const routes = [
  {
    path: '/',
    meta: { name: 1 },
    Header,
    Main: home,
    Footer
  },
  {
    path: '/games',
    meta: { name: 2 },
    Header,
    Main: loadable(() => import('@/views/games/games.js'))
  },
  {
    path: '/promotions',
    meta: { name: 3 },
    isPromoHeader: true,
    Main: loadable(() => import('@/views/promotions/promotions.js'))
  }
]
export const history = createBrowserHistory()

export default function Routes() {
  return (
    <Switch>
      {routes.map((route, index) => {
        return (
          <Route
            key={index}
            path={route.path}
            exact={!route.exact}
            render={props => <PageContainer route={route} routeProps={props}/>}/>
        )
      })}
      <Route path='*' component={Error404}/>
    </Switch>
  )
}

history.listen((location, action) => {
  // console.log(action, location)
})
