'use strict'

import qs from 'qs'
import md5 from 'js-md5'
import axios from 'axios'
import config from '@/config/config.js'
import { history } from '@/router/index.js'
import store from '@/store/index.js'
import { dialog } from '@/components/dialog/Dialog.js'
import { loading } from '@/components/loading/Loading.js'
import { getWebToken } from '@/store/actions/common.js'
import { setSignOut } from '@/store/actions/sign.js'
import { sixRandom, reverseString } from '@/utils/util.js'

// 创建 axios 实例
const _http = axios.create({
  timeout: 30 * 1000,
  baseURL: config.apiUrl,
  headers: {
    v: config.version,
    AppId: config.appId,
    domainName: config.apiUrl
  }
})

function setHeader(param, tokenObj = null) {
  tokenObj = (tokenObj && JSON.parse(window.atob(tokenObj)))
  const qid = md5(Date.now() + sixRandom())
  let sign = reverseString(JSON.stringify(param))
  sign += qid + config.appId + config.version
  sign += tokenObj ? (tokenObj.token + tokenObj.u2token) : ''
  return {
    Sign: md5(sign),
    Qid: qid,
    token: tokenObj ? tokenObj.token : null
  }
}

// 添加请求拦截器
_http.interceptors.request.use(async config => {
  const state = store.getState()
  config.data = { ...config.data }
  config.data.productId = 'A02'
  config._notShowErrorTipsCode = config.data._notShowErrorTipsCode
  config._isNeedLoading = config.data._isNeedLoading !== false
  config._isShowErrorTips = config.data._isShowErrorTips !== false
  config._isNeedLoading && loading.open()
  const tokenObj = config.data._isNeedToken !== false ? await store.dispatch(getWebToken()) : null
  if (config.data._isNeedLoginName !== false && !config.data.loginName) {
    config.data.loginName = state.customer.loginName
  }
  delete config.data._isNeedToken
  delete config.data._isNeedLoading
  delete config.data._isNeedLoginName
  delete config.data._notShowErrorTipsCode
  config.headers = {
    ...config.headers,
    ...setHeader(config.data, tokenObj)
  }
  if (~'application/x-www-form-urlencoded'.indexOf(config.headers['Content-Type'])) {
    config.data = qs.stringify(config.data)
  }
  return config
}, error => {
  loading.hide()
  console.log('request error:\n', error)
  return Promise.reject(error)
})

// 添加响应拦截器
_http.interceptors.response.use(response => {
  const { head } = response.data
  const code = head && head.errCode
  const errorMessage = head && head.errMsg
  const notTipsCode = ['0000', 'GW_802121']
  const needLoginCodes = ['GW_802154', 'GW_890206', 'GW_800102']
  const dialogId = window.btoa('_http.interceptors.response')
  const isTips = code && response.config._isShowErrorTips && code !== response.config._notShowErrorTipsCode && !~notTipsCode.indexOf(code)
  if (response.config._isNeedLoading) loading.hide()
  if (code && ~needLoginCodes.indexOf(code)) {
    dialog(errorMessage, dialogId)
    store.dispatch(setSignOut())
    history.push('/')
  } else if (isTips) {
    dialog(errorMessage, dialogId)
  }
  return response.data
}, (error) => {
  loading.hide()
  console.log('response error:\n', error)
  return Promise.reject(error)
})

// 封装常用请求方法
const http = {
  get(url, params = {}, headers = {}) {
    const options = {}
    options.params = params
    options.headers = headers
    return _http.get(url, options)
  },
  post(url, params, headers = {}) {
    return _http.post(url, params, { headers })
  },
  put(url, params, headers = {}) {
    return _http.put(url, params, { headers })
  },
  delete(url, params = {}, headers = {}) {
    const options = {}
    options.params = params
    options.headers = headers
    return _http.delete(url, options)
  }
}

export default http
