import api from '@/api/api'
import * as types from '@/store/actionTypes.js'

export function setCustomerInfo(customer = {}) {
  return {
    type: types.setCustomerInfo,
    state: { customer }
  }
}

export function setBalanceInfo(balances = {}) {
  return {
    type: types.setBalanceInfo,
    state: { balances }
  }
}

export function setSignOut(token = null) {
  return {
    type: types.setSignOut,
    state: {
      token,
      customer: {},
      balances: {}
    }
  }
}

// 登录
export function signIn(params = {}) {
  return dispatch => {
    return api.loginEx(params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        const params = {
          _isNeedLoading: false
        }
        dispatch(setCustomerInfo(body))
        dispatch(getCustomerInfo(params))
        dispatch(getBalanceInfo(params))
      }
      return {
        head,
        body
      }
    })
  }
}

// 获取用户信息
function getCustomerInfo(params = {}) {
  const defaultParams = {
    inclEmail: 1,
    inclCredit: 1,
    inclAddress: 1,
    inclCurrency: 1,
    inclRealName: 1,
    inclMobileNo: 1,
    inclEmailBind: 1,
    inclBtcAccount: 1,
    inclVerifyCode: 1,
    inclBankAccount: 1,
    inclFirstXmFlag: 1,
    inclNickNameFlag: 1,
    inclMobileNoBind: 1,
    inclPromoAmountByMonth: 1,
    inclRebatedAmountByMonth: 1,
    inclUpgradeRequiredBetAmount: 1
  }
  params = {
    ...defaultParams,
    ...params
  }

  return dispatch => {
    return api.getCustomerInfo(params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        dispatch(setCustomerInfo(body))
      }
      return {
        head,
        body
      }
    })
  }
}

// 获取余额信息
function getBalanceInfo(params) {
  return dispatch => {
    return api.getBalance(params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        dispatch(setBalanceInfo(body))
      }
      return {
        head,
        body
      }
    })
  }
}

export default {
  setCustomerInfo,
  setBalanceInfo,
  setSignOut,
  signIn,
  getCustomerInfo,
  getBalanceInfo
}
