import React, { useState, useEffect } from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames/bind'
import io from 'socket.io-client'
import config from '@/config/config.js'
import homeCss from '../home.module.scss'
import scss from './dewFigure.module.scss'

const cx = classNames.bind(scss)

function areEqual(prevProps, nextProps) {
  if (nextProps.event.event === 'resultList') return false
  return prevProps.code !== nextProps.event.vid
}

function _DewFigureItem(props) {
  const { event, name, code } = props
  const [tieTime, setTie] = useState(0) // 和局次数
  const [bankerTime, setBanker] = useState(0) // 庄赢次数
  const [playerTime, setPlayer] = useState(0) // 闲赢次数
  const [countdown, setCountdown] = useState(0) // 倒计时
  const [timerText, setTimerText] = useState('') // 游戏进度文字
  const [lastGameCode, setLastGameCode] = useState(null) // 最后一句游戏code
  const [showLoading, setShowLoading] = useState(true) // 显示加载
  const [showShuffle, setShowShuffle] = useState(true) // 显示洗牌
  const [resultList, setResultList] = useState([]) // 游戏结果

  const rows = 6
  const columns = 16
  const lineWidth = 1
  const cellSizeWidth = 23
  const cellSizeHeight = 23
  const origin = lineWidth / 2
  const cellWidth = cellSizeWidth + lineWidth
  const cellHeight = cellSizeHeight + lineWidth
  const gridWidth = columns * cellWidth + lineWidth
  const gridHeight = rows * cellHeight + lineWidth

  useEffect(() => {
    // 连接后推送的历史数据
    function formatResultList(obj) {
      let key
      let item
      const list = []
      if (!obj) return
      const roundRes = obj.roundRes || {}
      for (key in roundRes) {
        if (roundRes.hasOwnProperty(key)) {
          item = formatItemData(roundRes[key])
          list.push(item)
        }
      }
      if (!list.length) {
        newShoes()
      } else {
        newRound(obj.seconds)
        setResultList(list)
      }
      setShowLoading(false)
    }

    // 洗牌中
    function newShoes() {
      setCountdown(0)
      setShowShuffle(true)
      setResultList([])
      setTimerText('洗牌中')
    }

    // 开始一局
    function newRound(seconds) {
      setShowShuffle(false)
      setCountdown(seconds)
      setTimerText('结算中')
    }

    // 结束一局
    function closeRound(obj) {
      setCountdown(0)
      setTimerText('发牌中')
      setResultList(list => [
        ...list,
        formatItemData(obj)
      ])
      setLastGameCode(obj.gmcode)
    }

    switch (event.event) {
      case 'resultList': {
        return formatResultList(event.data[code])
      }
      case 'closeRound': {
        if (lastGameCode !== event.data.gmcode) closeRound(event.data)
        return
      }
      case 'newRound': {
        return newRound(event.data.seconds)
      }
      case 'newShoes': {
        return newShoes()
      }
    }
  }, [code, event, lastGameCode])

  useEffect(settingCanvas, [code, gridWidth, gridHeight])

  useEffect(countdownTimer, [countdown])

  useEffect(handelResultListChange, [resultList, code, cellHeight, cellWidth, gridHeight, gridWidth, origin])

  // 露珠图list变化
  function handelResultListChange() {
    drawDewFigure()
    let tie = 0
    let banker = 0
    let player = 0
    resultList.forEach(item => {
      tie += Number(item.result === 'tie')
      banker += Number(item.result === 'banker')
      player += Number(item.result === 'player')
    })
    setTie(tie)
    setBanker(banker)
    setPlayer(player)
  }

  // 倒计时
  function countdownTimer() {
    const timer = setTimeout(() => {
      setCountdown(countdown - 1)
    }, 1000)
    if (countdown <= 0) clearTimeout(timer)

    return () => clearTimeout(timer)
  }

  // 设置Canvas
  function settingCanvas() {
    const canvas = document.getElementById('canvas' + code)
    const ctx = canvas.getContext('2d')
    let dpr = window.devicePixelRatio
    if (dpr) {
      dpr = Math.ceil(dpr)
      dpr = dpr > 2 ? dpr : 4
      canvas.width = gridWidth * dpr
      canvas.height = gridHeight * dpr
      ctx.scale(dpr, dpr)
    }
    // formatResultList(socketResultList[code])
  }

  // 绘制露珠图
  function drawDewFigure() {
    const resultColor = {
      banker: '#d55263',
      player: '#6e8df7',
      tie: '#4ea950'
    }
    const bigRoadList = formatBigRoadData(resultList, resultColor).slice(-(columns - 1))
    const canvas = document.getElementById('canvas' + code)
    const ctx = canvas.getContext('2d')
    ctx.clearRect(0, 0, gridWidth, gridHeight)
    drawGrid({
      ctx,
      origin,
      gridWidth,
      cellWidth,
      gridHeight,
      cellHeight
    })
    bigRoadList.forEach((column, columnIndex) => {
      column.forEach((item, itemIndex) => {
        const x = (columnIndex + 1) * cellWidth - cellSizeWidth / 2
        const y = (itemIndex + 1) * cellHeight - cellSizeHeight / 2
        item.color && drawCircle(ctx, x, y, item.color)
        if (item.tieNumber) {
          const offset = 3
          const start = columnIndex * cellWidth + origin
          const end = itemIndex * cellHeight + origin
          drawLine(ctx, [start + offset, end + cellHeight - offset], [start + cellWidth - offset, end + offset], 2, resultColor.tie)
          if (item.tieNumber > 1 && item.tieNumber < 10) {
            drawText(ctx, Math.ceil(x), Math.ceil(y), '2', '16px Calibri')
          } else if (item.tieNumber > 10) {
            drawText(ctx, x, Math.ceil(y), '18', '12px Calibri')
          }
        }
      })
    })
    ctx.closePath()
  }

  // 格式化大路露珠图数据
  function formatBigRoadData(list, resultColor) {
    let column // 当前列
    let lastItem // 上一局
    let lastItemResult // 上一局结果
    let tieNumber = 0 // 和局次数
    let isAddColumn = false // 是否新增列
    const bigRoadList = [] // 大路图数据

    function addItem(item) {
      // 换列分两种情况，1、不同游戏结局 2、当前列满载
      const isAddColumn = getIsAddColumn.call(this, item, column)
      if (isAddColumn) {
        let hasExistColumn
        let nextColumn
        if (isAddColumn.addIndex !== undefined) { // 必须在当前列后延展
          let i
          item.isNewColumn = true
          for (i = bigRoadList.length; i--;) { // 查找当前列的下标
            if (column === bigRoadList[i]) {
              break
            }
          }
          nextColumn = bigRoadList[i + 1]
          if (nextColumn) column = nextColumn
        } else {
          hasExistColumn = bigRoadList.some(item => { // 在所有列中查找第一个值为空的列作为当前列
            if (!item[0]) return (column = item)
          })
        }
        if (!hasExistColumn && !nextColumn) { // 添加一列
          column = []
          bigRoadList.push(column)
        }
      }
      for (let i = 0; i < rows; i++) {
        if (isAddColumn.addIndex !== undefined) {
          return (column[isAddColumn.addIndex] = item)
        }
        if (!column[i]) return (column[i] = item)
      }
      console.error('游戏结局添加到列中失败')
    }

    function getIsAddColumn(item, column) {
      if (!column) { // 是否第一局
        isAddColumn = true
      } else if (item.result !== lastItemResult) { // 当次游戏结局是否和上次游戏结局不一次
        isAddColumn = true
      } else if (typeof isAddColumn === 'object') {
        return isAddColumn
      } else {
        isAddColumn = {
          isAddColumn: true,
          addIndex: rows - 1
        } // 默认当前列都被添加满了，需要新增列
        for (let i = 0; i < rows; i++) {
          if (!column[i]) {
            isAddColumn = false
            break
          } else if (column[i].isNewColumn) {
            isAddColumn = {
              isAddColumn: true,
              addIndex: i - 1
            }
            break
          }
        }
      }
      return isAddColumn
    }

    list.forEach(item => {
      if (item.result === 'tie') { // 和
        if (lastItem) {
          tieNumber = 0 // 重置和的次数
          lastItem.tieNumber += 1 // 平局计入上一局中
        } else {
          tieNumber += 1 // 如果没有上一局，就计数
        }
      } else {
        lastItem = {
          ...item,
          tieNumber,
          color: resultColor[item.result]
        }
        addItem.call(this, lastItem)
        tieNumber = 0
        lastItemResult = item.result
      }
    })

    if (tieNumber && !bigRoadList.length) {
      bigRoadList.push([{
        ...list[0],
        tieNumber
      }])
    }

    return bigRoadList
  }

  // 画网格线
  function drawGrid(params) {
    const { ctx, origin, gridWidth, cellWidth, gridHeight, cellHeight } = params
    const lineColor = '#202020'
    // 画竖线（列）
    for (let x = origin; x <= gridWidth; x += cellWidth) {
      ctx.beginPath()
      ctx.strokeStyle = lineColor
      ctx.lineWidth = lineWidth
      ctx.moveTo(x, 0)
      ctx.lineTo(x, gridHeight)
      ctx.stroke()
    }
    // 画横线（行）
    for (let y = origin; y <= gridHeight; y += cellHeight) {
      ctx.beginPath()
      ctx.strokeStyle = lineColor
      ctx.lineWidth = lineWidth
      ctx.moveTo(0, y)
      ctx.lineTo(gridWidth, y)
      ctx.stroke()
    }
  }

  // 画圆
  function drawCircle(ctx, x, y, color) {
    const circleFillColor = '#141414'
    const circleBorderColor = color
    const circleBorderWidth = 3
    const circleRadii = 8
    ctx.beginPath()
    ctx.arc(x, y, circleRadii, 0, 360, false)
    ctx.fillStyle = circleFillColor
    ctx.strokeStyle = circleBorderColor
    ctx.lineWidth = circleBorderWidth
    ctx.fill()
    ctx.stroke()
  }

  // 画斜线
  function drawLine(ctx, start, end, width, color) {
    ctx.beginPath()
    ctx.moveTo(start[0], start[1])
    ctx.lineTo(end[0], end[1])
    ctx.lineWidth = width
    ctx.strokeStyle = color
    ctx.stroke()
  }

  // 画文字
  function drawText(cxt, x, y, text, font) {
    cxt.beginPath()
    cxt.font = font
    cxt.fillStyle = '#fff'
    cxt.textAlign = 'center'
    cxt.textBaseline = 'middle'
    cxt.fillText(text, x, y)
    cxt.stroke()
  }

  // 格式化游戏对局数据
  function formatItemData(item) {
    const obj = {
      pair: item.pair // 对子 0：无、1：庄对、2：闲对、3：庄闲对
    }
    if (item.banker_val > item.player_val) {
      obj.result = 'banker' // 庄赢
    } else if (item.banker_val < item.player_val) {
      obj.result = 'player' // 闲赢
    } else {
      obj.result = 'tie' // 和
    }
    return obj
  }

  return (
    <div className={cx('dew__item')}>
      <div className={cx('dew__header', 'clearfix')}>
        <span className={'fl'}>{name}桌</span>
        <div className={cx('dew__timer')}>
          <i className={cx('dew__icon-time')}/>
          {
            countdown
              ? <span className="monospaced-font">00:{countdown > 9 ? countdown : '0' + countdown}</span>
              : <span>{timerText || '--'}</span>
          }
        </div>
      </div>
      <div className={cx('dew__scoreboard', 'clearfix')}>
        <span className={cx('dew__scoreboard-item')}><span>庄</span><span>{bankerTime}</span></span>
        <span className={cx('dew__scoreboard-item')}><span>和</span><span>{tieTime}</span></span>
        <span className={cx('dew__scoreboard-item')}><span>闲</span><span>{playerTime}</span></span>
      </div>
      <div className={cx('dew__canvas-wrapper')}>
        <canvas id={'canvas' + code} className={cx('dew__canvas')}/>
        <div className={cx('dew__shuffle', { hide: !showShuffle })}>
          <span className={cx('dew__shuffle-text')}>洗牌中</span>
        </div>
        <div className={cx('dew__loading', { hide: !showLoading })}>
          <span className={cx('dew__loading-text')}>loading...</span>
        </div>
      </div>
      <div className={cx('dew__button-wrapper')}>
        <button className={homeCss.home__button}>快速入桌</button>
      </div>
    </div>
  )
}

_DewFigureItem.propTypes = {
  event: PropTypes.object.isRequired,
  name: PropTypes.string.isRequired,
  code: PropTypes.string.isRequired
}

const tables = [
  {
    name: 'D51',
    code: 'D051'
  },
  {
    name: 'D52',
    code: 'D052'
  },
  {
    name: 'D53',
    code: 'D053'
  }
]
const DewFigureItem = React.memo(_DewFigureItem, areEqual)
export default function DewFigure() {
  const [event, setEvent] = useState({})

  useEffect(initSocket, [])

  function initSocket() {
    const socket = io(config.socketUrl)

    function handelResultList(data) {
      data = JSON.parse(data) || {}
      let socketResultList = {}
      for (const key in data) {
        if (Object.hasOwnProperty.call(data, key) && tables.some(item => item.code === key)) {
          socketResultList = {
            ...socketResultList,
            [key]: data[key]
          }
        }
      }
      setEvent({
        event: 'resultList',
        data: socketResultList
      })
      // console.log('handelResultList')
    }

    function handelCloseRound(data) {
      data = JSON.parse(data) || {}
      if (tables.some(item => item.code === data.vid)) {
        setEvent({
          data,
          vid: data.vid,
          event: 'closeRound'
        })
        // console.log('handelCloseRound')
      }
    }

    function handelNewRound(data) {
      data = JSON.parse(data) || {}
      if (tables.some(item => item.code === data.vid)) {
        setEvent({
          data,
          vid: data.vid,
          event: 'newRound'
        })
        // console.log('handelNewRound')
      }
    }

    function handelNewShoes(data) {
      data = JSON.parse(data) || {}
      if (tables.some(item => item.code === data.vid)) {
        setEvent({
          data,
          vid: data.vid,
          event: 'newShoes'
        })
        // console.log('handelNewShoes')
      }
    }

    socket.on('result list', handelResultList)
    socket.on('close round', handelCloseRound)
    socket.on('new round', handelNewRound)
    socket.on('new shoes', handelNewShoes)
    return () => socket.destroy()
  }

  return (
    <div>
      {tables.map(item => <DewFigureItem event={event} code={item.code} name={item.name} key={item.code}/>)}
    </div>
  )
}
