export default function mapState(store) {
  const cacheState = {}
  const mapStateKey = [
    'token',
    'customer',
    'balances',
    'gameStatus'
  ]
  const state = store.getState()
  const cacheTime = Date.now() + 30 * 60 * 1000
  mapStateKey.forEach(key => {
    cacheState[key] = state[key]
  })
  cacheState._cacheTime = cacheTime
  window.localStorage.setItem('redux_state', JSON.stringify(cacheState))
}
