import http from '@/api/http.js'
import { baseGateway, productGateway } from '@/api/api_config.js'

// 获取百家乐排行榜
export function getBaccaratRank(params, headers) {
  return http.post(`${productGateway}/baccarat/get-new-rank`, params, headers)
}

// 获取AG旗舰在线人数
export function getOnlineNum(params, headers) {
  return http.post(`${baseGateway}/game/getOnlineNum`, params, headers)
}

export default {
  getBaccaratRank,
  getOnlineNum
}
