import React from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import classNames from 'classnames/bind'

import api from '@/api/api.js'
import {
  validatePhoneNum,
  validateLoginName,
  validatePassword
} from '@/utils/validate.js'
import { toggleLoginDialog } from '@/store/actions/common.js'
import { signIn } from '@/store/actions/sign.js'
import CodeButton from '@/components/code_button/CodeButton.js'
import scss from './loginDialog.module.scss'

const cx = classNames.bind(scss)

function mapStateToProps(state) {
  return {
    showLoginDialog: state.showLoginDialog
  }
}

class LoginDialog extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      image: null,
      isNeedCaptcha: false,
      errorMessage: null,
      isPasswordInput: true,
      loginForm: {
        loginName: '',
        verifyStr: '',
        messageId: null,
        captcha: '',
        captchaId: null
      }
    }
  }

  getIsNeedCaptcha() {
    const params = {
      _isNeedLoading: false
    }
    api.getIsNeedCaptcha(params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        const isNeedCaptcha = !!body.needCaptcha
        if (isNeedCaptcha !== this.state.isNeedCaptcha) {
          this.setState({
            isNeedCaptcha
          })
        }
        if (isNeedCaptcha) this.getCaptcha()
      }
    })
  }

  getCaptcha() {
    const params = {
      _isNeedLoading: false
    }
    api.getCaptcha(params).then(({ head, body }) => {
      if (head.errCode === '0000') {
        this.setState({
          loginForm: {
            ...this.state.loginForm,
            captchaId: body.captchaId
          },
          image: 'data:image/jpeg;base64,' + body.image
        })
      }
    })
  }

  handelInputChange(value, field) {
    this.setState({
      loginForm: {
        ...this.state.loginForm,
        [field]: value
      }
    })
  }

  sendCallBack(messageId) {
    this.setState({
      loginForm: {
        ...this.state.loginForm,
        messageId
      }
    })
  }

  validateForm() {
    let errorMessage = null
    const { loginForm } = this.state
    if (validatePhoneNum(loginForm.loginName)) {
      if (loginForm.messageId) {
        if (loginForm.verifyStr.length !== 6) {
          errorMessage = '请输入6位短信验证码'
        }
      } else {
        errorMessage = '请发送短信验证码登录'
      }
    } else if (validateLoginName(loginForm.loginName)) {
      if (!validatePassword(loginForm.verifyStr)) {
        errorMessage = '密码由6-16位数字或字母组成'
      }
    } else {
      errorMessage = '游戏账号/手机号不正确，请重新输入'
    }
    if (!errorMessage && loginForm.captchaId && loginForm.captcha.length !== 4) {
      errorMessage = '请输入4位图形验证码'
    }
    this.setState({ errorMessage })
    return !errorMessage
  }

  submit(e) {
    if (e.type === 'keypress' && e.which !== 13) return
    if (!this.validateForm()) return
    const params = {
      ...this.state.loginForm,
      _isShowErrorTips: false
    }
    this.props.dispatch(signIn(params)).then(({ head }) => {
      if (head.errCode === '0000') {
        this.props.dispatch(toggleLoginDialog(false))
      } else {
        this.getIsNeedCaptcha()
        this.setState({
          errorMessage: head.errMsg
        })
      }
    })
  }

  componentDidMount() {
    this.getIsNeedCaptcha()
  }

  shouldComponentUpdate(nextProps, nextState, nextContext) {
    // console.log('shouldComponentUpdate=>')
    // console.log('state=>', this.state)
    // console.log('nextState=>', nextState)
    return true
  }

  componentDidUpdate() {
    console.log('<====componentDidUpdate===>')
  }

  render() {
    const { dispatch } = this.props
    const { image, loginForm, isPasswordInput, errorMessage, isNeedCaptcha } = this.state
    const inputType = isPasswordInput ? 'password' : 'text'
    const sendCodeParams = {
      use: 2,
      mobileNo: loginForm.loginName
    }
    const enableCodeButton = validatePhoneNum(loginForm.loginName)

    return (
      <div className={cx('login')}>
        <div className={cx('login__box')}>
          <div className={cx('login__header')}>
            <span>贵宾您好，欢迎登录凯发娱乐</span>
            <button onClick={() => dispatch(toggleLoginDialog(false))} className={cx('login__close')}>×</button>
          </div>
          <div className={cx('login__content')}>
            {
              errorMessage &&
              <div className={cx('login__input-wrapper')}>
                <p className={cx('login__error')}>
                  <i className={cx('login__icon-error')}/>
                  <span>{errorMessage}</span>
                </p>
              </div>
            }
            <div className={cx('login__input-wrapper')}>
              <i className={cx('login__prefix', 'login__prefix--user')}/>
              <i className={cx('login__prefix', 'login__prefix--line')}/>
              <input value={loginForm.loginName}
                onChange={(e) => this.handelInputChange(e.target.value, 'loginName')}
                className={cx('login__input')}
                placeholder="游戏账号/手机号" maxLength="12"
                type="text"/>
              {
                loginForm.loginName &&
                <span onClick={() => this.handelInputChange('', 'loginName')}
                  className={cx('login__suffix', 'login__suffix--cancel')}>×</span>
              }
            </div>
            <div className={cx('login__input-wrapper')}>
              <i className={cx('login__prefix', 'login__prefix--password')}/>
              <i className={cx('login__prefix', 'login__prefix--line')}/>
              <input value={loginForm.verifyStr}
                onKeyPress={this.submit.bind(this)}
                onChange={(e) => this.handelInputChange(e.target.value, 'verifyStr')}
                className={cx('login__input', 'login__input--code')}
                placeholder="密码/验证码" maxLength="16"
                type={inputType}/>
              <CodeButton enable={enableCodeButton} params={sendCodeParams}
                sendCodeBefore={() => enableCodeButton}
                sendCallBack={this.sendCallBack.bind(this)}
                className={cx('login__code')}/>
              <i onClick={() => this.setState({ isPasswordInput: !isPasswordInput })}
                className={cx('login__suffix', [isPasswordInput ? 'login__suffix--eye-close' : 'login__suffix--eye-open'])}/>
            </div>
            {
              isNeedCaptcha &&
              <div className={cx('login__input-wrapper')}>
                <i className={cx('login__prefix', 'login__prefix--verify')}/>
                <i className={cx('login__prefix', 'login__prefix--line')}/>
                <input value={loginForm.captcha}
                  onKeyPress={this.submit.bind(this)}
                  onChange={(e) => this.handelInputChange(e.target.value, 'captcha')}
                  className={cx('login__input', 'login__input--code')}
                  placeholder="验证码" maxLength="4"
                  type="text"/>
                <img onClick={() => this.getCaptcha()} className={cx('login__code')} src={image} alt=""/>
              </div>
            }
            <p className={cx('login__forgot')}>
              <a className={cx('login__link')} href="javascript:void (0);">忘记账号、密码?</a>
            </p>
            <button onClick={this.submit.bind(this)} className={cx('login__button')}>立即登录</button>
            <p className={cx('login__registered')}>
              <span>还没有凯发账户？点击&nbsp;</span>
              <a className={cx('login__link', 'c-primary')} href="javascript:void (0);">极速开户</a>
            </p>
          </div>
        </div>
      </div>
    )
  }
}

LoginDialog.propTypes = {
  dispatch: PropTypes.func.isRequired,
  showLoginDialog: PropTypes.bool.isRequired
}

export default connect(mapStateToProps)(LoginDialog)
